.friendList {
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin: 30px auto auto;
    padding: 20px;
    width: 300px;
}

.item {
    align-items: center;
    box-shadow: 0 1px 2px 0 rgb(60 64 67 / 30%), 0 2px 6px 2px rgb(60 64 67 / 15%);
    display: flex;
    gap: 6px;
    padding: 5px;
}

.avatar {
    margin-right: 15px;
    margin-left: 5px;
    background-color: bisque;
    border-radius: 6px;
}

.status {

    display: block;
    height: 12px;
    width: 12px;
    border-radius: 100%;
}